import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpClientModule } from '@angular/common/http';
import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireAuthModule } from '@angular/fire/auth';
import { firebaseConfig } from './../credentials/credentials';
import { Firebase } from '@ionic-native/firebase';

import { ComponentsModule } from '../components/components.module';
import { UniqueDeviceID } from '@ionic-native/unique-device-id';
import { AndroidPermissions } from '@ionic-native/android-permissions';

import { MyApp } from './app.component';
import { DynaPage } from '../pages/dyna/dyna';
import { LoginPage } from '../pages/login/login';
import { ErrorPage } from '../pages/error/error';
import { ApprovalPage } from '../pages/approval/approval';
import { AmHomePage } from '../pages/am-home/am-home';
import { SpHomePage } from '../pages/sp-home/sp-home';
import { UserHomePage } from '../pages/user-home/user-home';
import { ItineraryPage } from '../pages/itinerary/itinerary';
// import { GenricPopupPage } from '../pages/genric-popup/genric-popup';
// import { DatePicker } from '@ionic-native/date-picker';

import { UsernameValidator } from '../validators/username';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { FirestoreProvider } from '../providers/firestore/firestore';
import { OtuAuthProvider } from '../providers/otu-auth/otu-auth';
import { OtuConfigProvider } from '../providers/otu-config/otu-config';
import { OtuLoggerProvider } from '../providers/otu-logger/otu-logger';
import { OtuDeviceProvider } from '../providers/otu-device/otu-device';

@NgModule({
  declarations: [
    MyApp,
    // DynaPage,
    LoginPage,
    ErrorPage,
    // ApprovalPage,
    // AmHomePage,
    // SpHomePage,
    // UserHomePage,
    // ItineraryPage,
    // GenricPopupPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    ComponentsModule,
    AngularFirestoreModule,
    AngularFireModule.initializeApp(firebaseConfig),
    // AngularFirestoreModule.enablePersistence(),
    AngularFireAuthModule,
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    // DynaPage,
    LoginPage,
    ErrorPage,
    // ApprovalPage,
    // AmHomePage,
    // SpHomePage,
    // UserHomePage,
    // ItineraryPage,
    // GenricPopupPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    FirestoreProvider,
    OtuAuthProvider,
    OtuConfigProvider,
    UsernameValidator,
    OtuLoggerProvider,
    Firebase,
    OtuDeviceProvider,
    UniqueDeviceID,
    AndroidPermissions
    // DatePicker
  ]
})
export class AppModule {}



