import { Component, Input } from '@angular/core';
import { Label } from 'ionic-angular';

/**
 * Generated class for the ShowPointsComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'show-points',
  templateUrl: 'show-points.html'
})
export class ShowPointsComponent {

  @Input('list') list;
  @Input('label') label;
  text: string;
  showDetails: boolean = false;

  constructor() {
    console.log('Hello ShowPointsComponent Component');
    this.text = 'Hello World';
  }
  ngOnInit() {
    console.log("Hello ngOnInit");  
  }
  foo() {
    this.showDetails = !this.showDetails;
    console.log(this.list);
  }
}



