import { FirestoreProvider } from './../../providers/firestore/firestore';
import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular';

/**
 * Generated class for the ProfileComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'profile',
  templateUrl: 'profile.html'
})
export class ProfileComponent {

  userInfo;
  userInfo1 = {
    name: "Sudhir Bhandary",
    email: "bhandarys@seva.com",
    mob: "+91.1234567890",
    others: "I am a bird watcher",
    img: "assets/imgs/bheem.jpg",
    address: {
      firstLine: "Roop Nagar, Prem Gali",
      secondLine: "Kholi No. 420",
      city: "Mumbai",
      pin: "420 420",
      state: "MH",
      country: "India",
    }
  }
  text: string;
  user$;

  constructor(  private alertCtrl: AlertController
              , private firestore: FirestoreProvider
              ) {
    console.log('Hello ProfileComponent Component');
    this.text = 'Hello World';
    this.user$ = this.firestore.getCurrentUser();
    this.user$.subscribe((u) => this.userInfo = u);
  }
  edit(field) {
    let label;
    let input;
    switch(field) {
      case 'img': {
        input = [{
          name: 'img',
          value: this.userInfo.img
        }]
        label = "Profile Picture";
        break;
      }
      case 'name': {
        input = [{
          name: 'name',
          value: this.userInfo.name,
          placeholder: "Name"
        }]
        label = "Name";
        break;
      }
      case 'mob': {
        input = [{
          name: 'mob',
          value: this.userInfo.mob,
          placeholder: "Mobile Number"
        }]
        label = "Mobile Number";
        break;
      }
      case 'whatsapp': {
        input = [{
          name: 'whatsapp',
          value: this.userInfo.mob,
          placeholder: "whatsapp Number"
        }]
        label = "whatsapp Number";
        break;
      }
      case 'me': {
        input = [{
          name: 'me',
          value: this.userInfo.others,
          placeholder: "About Myself"
        }]
        label = "About Myself";
        break;
      }
      case 'address': {
        if(this.userInfo.address == undefined) {
          this.userInfo['address'] = {}
        }
        input = [{
          name: 'firstline',
          value: this.userInfo.address.firstLine,
          placeholder: "Address Line 1"
        },
        {
          name: 'secondline',
          value: this.userInfo.address.secondLine,
          placeholder: "Address Line 2"
        },
        {
          name: 'city',
          value: this.userInfo.address.city,
          placeholder: "City"
        },
        {
          name: 'pin',
          value: this.userInfo.address.pin,
          placeholder: "Pin"
        },
        {
          name: 'state',
          value: this.userInfo.address.state,
          placeholder: "State"
        },
        {
          name: 'country',
          value: this.userInfo.address.country,
          placeholder: "Country"
        }]
        label = "Address";
        break;
      }
    }
    let data = this.alertCtrl.create({
      title: `Modify ${label}`,
      inputs: input,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            switch(field) {
              case 'name': {
                  this.userInfo.name = data.name;
                break;
              }
              case 'mob': {
                this.userInfo.mob = data.mob;
                break;
              }
              case 'whatsapp': {
                this.userInfo.whatsapp = data.whatsapp;
                break;
              }
              case 'me': {
                this.userInfo.others = data.me
                break;
              }
              case 'img': {
                this.userInfo.img = data.img;
                break;
              }
              case 'address': {
                  this.userInfo.address.firstLine = data.firstline;
                  this.userInfo.address.secondLine = data.secondline;
                  this.userInfo.address.city = data.city;
                  this.userInfo.address.pin = data.pin;
                  this.userInfo.address.state = data.state;
                  this.userInfo.address.country = data.country;
                break;
              }
              default: {
                alert('Report to police');
              }
            }
            this.firestore.saveUser(this.userInfo);
          } // till here
        }
      ]
    });
    data.present();
  }
}



