import { Component, Input, Output, EventEmitter } from '@angular/core';
import { FindValueSubscriber } from 'rxjs/internal/operators/find';

/**
 * Generated class for the StringArrayComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'string-array',
  templateUrl: 'string-array.html'
})
export class StringArrayComponent {

  text: string;
  @Input('list') list;
  @Input('label') label;
  @Input('lang') lang;

  val;
  buttonLabel = "Add";
  editMode: boolean = false;
  index: number = -1;

  constructor() {
    console.log('Hello StringArrayComponent Component');
    this.text = 'Hello World';
  }

  add() {
    if(this.editMode && this.index > -1 ) {
      this.list[this.index] = this.val;
      this.editMode = false;
      this.index = -1;
      this.buttonLabel = "Add";
      } else {
      this.list.push(this.val);
    }
    this.val = "";
    console.log(`The length is ${this.list.length}`);
  }
  remove(i) {
    this.list.splice(i, 1);
  }
  edit(i) {
    this.val = this.list[i];
    this.editMode = true;
    this.index = i;
    this.buttonLabel = "Modify";
  }
}



