import { FirestoreProvider } from './../firestore/firestore';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Firebase } from '@ionic-native/firebase';
import { OtuDeviceProvider } from '../otu-device/otu-device';
import { Nav, Platform } from 'ionic-angular';

/*
  Generated class for the OtuLoggerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class OtuLoggerProvider {

  events = ['Itinerary clicked'];
  device;
  location = {};

  constructor(
                private fba: Firebase
              , private firestore: FirestoreProvider
              , private unique: OtuDeviceProvider
              , private platform: Platform
             ) {
    console.log('Hello OtuLoggerProvider Provider');
  }

  async eventLog(event, code) {
    console.log("eventLog");
    if(this.platform.is('cordova')) {
      this.eventLogFB(event, code);
      this.eventLogFS(event, code);
      console.log("On device");
    } else {
      console.log("On Windows");
      
    }
  }
  async eventLogFS(event, code) {
    // Need to figure out a way by which this info can be provided without specific permission
    // if(this.device == undefined) {
      // this.device = this.unique.getDeviceId().then((d) => {
        this.device = {};
        this.saveEvent(event, code, 'Success');
        console.log('eventLogFS is success'); 
      // }).catch(e => {
      //   console.log(e);     
      // })
    // } else {
    //   this.saveEvent(event, code, 'Success');
    // }
  }
  async eventLogFB(event, code) {
    code['timestamp'] = new Date();
    this.fba.logEvent(event, {
      code: code
    }).then((s) => {
      console.log("Success");
      console.log(s);
    }, (e) => {
      console.log(e);
    })
  }

  async saveEvent(event, code, e) {
    this.firestore.eventLog({
      event: event,
      code: code,
      device: this.device,
      readStatus: e,
      location: this.location
    })
  }

}



