import { FirestoreProvider } from './../../providers/firestore/firestore';
import { Component, Input } from '@angular/core';

/**
 * Generated class for the ProgressBarComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'progress-bar',
  templateUrl: 'progress-bar.html'
})
export class ProgressBarComponent {

  text: string;
  @Input('progress') progress;
  @Input('startTime') startTime;
  
  constructor(private firestore: FirestoreProvider) {
    console.log('Hello ProgressBarComponent Component');
    this.text = 'Hello World';
  }

  getTimeInTimeFormat(n) {
    
    if(n == 0) {
      return "00:00";
    }
    let d = new Date(n.toDate());
    let h = d.getUTCHours();
    let m = d.getUTCMinutes();

    let s = '';
    if(h < 10) {
      s = '0';
    }
    s += h.toString() + ':';
    if(m < 10) {
      s += '0';
    }
    s += m.toString();
    return s;
  }
  getProgress(n) {
    if(n == undefined || n == 0) {
      return 0;
    }
    console.log(`n.seconds = ${n.seconds}; this.startTime.seconds = ${this.startTime.seconds}`);
    
    return Math.floor((n.seconds - this.startTime.seconds)/(24*60*60));
  }

}



