import { FirestoreProvider } from './../providers/firestore/firestore';
import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { OtuAuthProvider } from '../providers/otu-auth/otu-auth';
import { OtuConfigProvider } from '../providers/otu-config/otu-config';

import { LoginPage } from '../pages/login/login';
import { DynaPage } from '../pages/dyna/dyna';
import { ApprovalPage } from '../pages/approval/approval';
import { AmHomePage } from '../pages/am-home/am-home';
import { SpHomePage } from '../pages/sp-home/sp-home';
import { UserHomePage } from '../pages/user-home/user-home';
import { ItineraryPage } from '../pages/itinerary/itinerary';

import { Location } from '@angular/common';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any;

  pages: Array<{ title: string, component: any, label: {en: string, kn: string} }>;

  loggedin: boolean = false;
  lang: string;
  lang$: any;

  loginLabel = {en: "Login", kn: "ಲಾಗಿನ್"};
  logoutLabel = {en: "Logout",kn: "ಲಾಗ್ ಔಟ್"};

  constructor(
    public platform: Platform
    , public statusBar: StatusBar
    , public splashScreen: SplashScreen
    , private auth: OtuAuthProvider
    , public config: OtuConfigProvider
    , public firestore: FirestoreProvider
    , private location: Location
    ) {

    this.initializeURLParams();

    this.lang$ = this.config.getLanguage().subscribe((l) => this.lang = l)
    this.auth.getUser().subscribe((u) => {
      if (u == undefined || u == null) {
        this.pages = []
        this.pages.push({ title: 'UserItineraryPage', component: 'UserItineraryPage', label: {en: 'Itinerary', kn: 'Itinerary'} });
        this.rootPage = "UserItineraryPage";
        // this.rootPage = LoginPage;
        this.loggedin = false;
      } else {
        this.firestore.getUser(u.email).valueChanges().subscribe((user) => {
          if(user.length > 0) {
            this.config.getMenuItems(user[0]['role']).valueChanges().subscribe((pc) => {
              if(pc.length > 0) {
                this.rootPage = pc[0]['menu']['root'];
                this.pages = pc[0]['menu']['menu'];
              }
              this.pages.push({ title: 'UserItineraryPage', component: 'UserItineraryPage', label: {en: 'Itinerary', kn: 'Itinerary'} });
              this.rootPage = "UserItineraryPage";
              this.loggedin = true;
            });
          } else {
            this.pages.push({ title: 'UserItineraryPage', component: 'UserItineraryPage', label: {en: 'Itinerary', kn: 'Itinerary'} });
            this.rootPage = "UserItineraryPage";
            this.loggedin = false;
            this.auth.logout();
            // this.rootPage = LoginPage;
          }
        });
      }
    })
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    // if (page.title == "Login") {
    //   this.auth.logout().then((s) => {
        //Now I don't know what to do
    //     this.loggedin = false;
    //   })
    // }
    this.nav.setRoot(page.component);
  }
  login() {
    if (this.loggedin) {
      this.auth.logout().then((l) => {
        // What do I do now?
        this.loggedin = false;
      })
    } else {
      this.nav.setRoot(LoginPage);
    }
  }
  initializeURLParams() {
    const path = this.location.path(true),
      hasParams = /\?(.+?\=.+){1}/;
    let params;
  
    if (hasParams.test(path)) {
      params = {};
      path.split('?')[1].split('&').forEach(both => {
        let e = both.split('=');
        params[e[0]] = e[1];
      });
    }
  
    this.firestore.setItrCode(params)
  }
}


