import { Injectable } from '@angular/core';
import { FirestoreProvider } from './../providers/firestore/firestore';
import { FormControl } from '@angular/forms';
import { OtuConfigProvider } from '../providers/otu-config/otu-config';

@Injectable()
export class UsernameValidator {

  constructor(
                public firestore: FirestoreProvider
              , public config: OtuConfigProvider
              ) {}

  checkReference(control: FormControl): any {

    return new Promise(resolve => {
      this.firestore.getUser(control.value).valueChanges().subscribe((u) => {
        
        if(u != undefined && u.length > 0 && u[0]['canAdd'] != undefined && u[0]['canAdd'].length > 0) {
          this.firestore.setReferenceId(u[0]['id']);
          resolve(null);
        } else {
          resolve('Not a valid reference');
        }
      })
      resolve(null);
    });
  }
  static checkUsername(control: FormControl): any {

    return new Promise(resolve => {

      //Fake a slow response from server

      setTimeout(() => {
        if(control.value.toLowerCase() === "greg"){

          resolve({
            "username taken": true
          });

        } else {
          resolve(null);
        }
      }, 2000);

    });
  }

}


