import { Component, Input, Output, EventEmitter } from '@angular/core';
import { AlertController } from 'ionic-angular';
/**
 * Generated class for the DisplayDaysComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'display-days',
  templateUrl: 'display-days.html'
})
export class DisplayDaysComponent {

  text: string;
  test = [];
  @Input('numberOfDays') numberOfDays;
  @Output('day') day = new EventEmitter();
  @Output('dayEvent') dayEvent = new EventEmitter();

  constructor(
                public alertCtrl: AlertController
              ) {
    console.log('Hello DisplayDaysComponent Component');
    this.text = 'Hello World'; 
  }
  ngOnInit() {
    console.log(this.test);
  }
  getNumberArray() {
    return Array(this.numberOfDays);
  }
  dayClicked(i) {
    this.day.emit(i);
  }
  press(i) {
    let soundAlert = this.alertCtrl.create({
      message: `Do you want to shift the day?`,
      buttons: [
        {
          text: "Delete",
          handler: () =>{
            this.dayEvent.emit({event: 'delete', day: i});
          }
        },
        {
          text: 'Shift Left',
          handler: () => {
            this.dayEvent.emit({event: 'left', day: i});
          }
        },
        {
          text: 'Shift Right',
          handler: () => {
            this.dayEvent.emit({event: 'right', day: i});
        }
      }
      ]
    });
    soundAlert.present();
  }
}



