import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument, DocumentReference } from 'angularfire2/firestore';
import { OtuAuthProvider } from '../otu-auth/otu-auth';

/*
  Generated class for the OtuConfigProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class OtuConfigProvider {

  public lang$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  public menu$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  lang: string;

  constructor(
                public firestore: AngularFirestore
              , public auth: OtuAuthProvider

              ) {

    console.log('Hello OtuConfigProvider Provider');
    this.lang = "en";
    this.lang$.next(this.lang);
  }
  getLanguage() {
    return this.lang$;
  }
  setLanguage(lang) {
    this.lang = lang;
    this.lang$.next(lang);
  }
  getFields(role){
    return this.firestore.collection('fields', ref => ref.where("appId", "==", this.getAppId()).where("role", "==", role));
  }
  getErrorMessages(role) {
    return this.firestore.collection('errorMessages', ref => ref.where("appId", "==", this.getAppId()).where("role", "==", role));
  }
  getMenuItems(role) {
    console.log(`getMenuItems ${role}`);
    return this.firestore.collection('pageConfig', ref => ref.where("name", "==", role).where("appId", "==", this.getAppId()));
  }
  getPageDetails(role, page) {
    console.log(`getMenuItems ${role}`);
    return this.firestore.collection('pageConfig', ref => ref.where("name", "==", role)
    // .where("pages.name", "==", page)
    .where("appId", "==", this.getAppId()));
  }
  logFire(log) {
    let id = this.firestore.createId()
    this.firestore.collection('logs').doc(id).set({
      id,
      log
    })
  }
  getCurrentMenu() {
    this.auth.getUser().subscribe((au) => {
      if (au == undefined || au == null) {
        this.menu$.next([]);
      } else {
        this.firestore.collection('Users', ref => ref.where("email", "==", au.email).limit(1))
            .valueChanges().subscribe((u) => {
              if(u != undefined && u.length > 0) {
                this.firestore.collection('pageConfig', ref => ref.where("name", "==", u[0]['role']).limit(1))
                .valueChanges().subscribe((m) => {
                  if(m != undefined && m.length > 0) {
                    this.menu$.next(m);
                  }
                });
              } else {
                this.menu$.next([]);
              }
            })
        }
      });
    return this.menu$;
  }
  getAppId() {
    return this.getAppIdWithDetails().appId;
  }
  getAppIdWithDetails() {
    return {
      appId: 2,
      ref: 'alfresco@seva.com',
      appliedFor: 'cust'
    }
  }
}



