import { OtuAuthProvider } from './../otu-auth/otu-auth';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/toPromise';
// import { forkJoin } from "rxjs//observable/forkJoin";
import 'rxjs/add/observable/forkJoin';
import { map } from "rxjs/operators";

import { combineLatest } from 'rxjs/observable/combineLatest';
import * as firebase from 'firebase/app';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument, DocumentReference } from 'angularfire2/firestore';
import { Task } from '../../models/task.interface';
// import { e } from '@angular/core/src/render3';
import { BehaviorSubject } from 'rxjs';
import { OtuConfigProvider } from '../../providers/otu-config/otu-config';

/*
  Generated class for the FirestoreProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FirestoreProvider {

  referenceId;
  public user$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  public refUser$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  sps$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  cust$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  coord$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  itrcode;

  constructor(public http: HttpClient
              , public firestore: AngularFirestore
              , public auth: OtuAuthProvider
              , public config: OtuConfigProvider
              ) {
    console.log('Hello FirestoreProvider Provider');
    this.user$.next(null);
    this.refUser$.next(null);
    this.sps$.next([]);
    this.cust$.next([]);
    this.coord$.next([]);
  }
  createTask( title, description, location, date, time, person, reminder, frequency, status, shareWith, shareList, userName) : Promise<void> {

    const id = this.firestore.createId();
    // let i: Promise<any>;
    // return i;
    console.log("createTask - " + userName);
    return this.firestore.doc(`taskList/${id}`).set({
      id,
      title,
      description,
      location,
      date,
      person,
      reminder,
      frequency,
      status,
      shareWith,
      shareList,
      userName});
  }

  getMyTaskList(userName): AngularFirestoreCollection<Task> {
    console.log("getMyTaskList - " + userName);
    return this.firestore.collection('taskList',  ref => ref.where('userName', '==', userName.toLowerCase()));
  }

  getSharedTaskList(shareWith: string): AngularFirestoreCollection<Task> {
    console.log("getSharedTaskList - " + shareWith);
    return this.firestore.collection('taskList',  ref => ref.where("shareList", "array-contains", shareWith.toLowerCase()));
  }

  getTaskDetail(taskId: string): AngularFirestoreDocument<Task> {
    return this.firestore.collection('taskList').doc(taskId);
  }
  deleteTask(taskId: string): Promise<void> {
    return this.firestore.doc(`taskList/${taskId}`).delete();
  }

  updateCCEntry(amount, location, date, avlBal, curOS, _id, smsDate) {
    let query = this.firestore.collection(`creditCardEntries`, 
              ref => ref.where('_id', "==", _id).where("smsDate", "==", smsDate).limit(1));
    query.get().subscribe((q) => {
      if(q.empty) {
        this.createCCEntry( amount, location, date, avlBal, curOS, _id, smsDate);
      }
    });
  }
  createCCEntry( amount, location, date, avlBal, curOS, _id, smsDate) : Promise<void> {

    const id = this.firestore.createId();
    return this.firestore.doc(`creditCardEntries/${id}`).set({
      id,
      amount,
      location,
      date,
      avlBal,
      curOS,
      _id, 
      smsDate
    });
  }
  log(tsFile, method, description, additionalInfo) : Promise<void> {

    const id = this.firestore.createId();
    // let i: Promise<any>;
    // return i;
    return this.firestore.doc(`otuLogger/${id}`).set({
      id,
      tsFile,
      method,
      description,
      additionalInfo});
  }

  getCCEntries(sort: boolean): AngularFirestoreCollection<Task> {
    let str: string;
    if(sort) {
      return this.firestore.collection('creditCardEntries', ref => ref.orderBy("date", "desc"));
    } else {
      return this.firestore.collection('creditCardEntries', ref => ref.orderBy("date"));
    }
  }
  updateUserProfile(user) {

    return this.firestore.doc<any>(`Users/${user.uid}`).update(user);
  }
  getUserProfile(uid): AngularFirestoreDocument<any> {
    console.log("getUserProfile");

    return this.firestore.collection('Users').doc(uid);
  }
  updateTask(taskId, task) {
    this.firestore.doc<any>(`taskList/${taskId}`).update(task);
  }
  deleteCC(ids) {
    ids.forEach(x => {
      if(x.selected != undefined && x.selected == true) {
        this.firestore.collection(`creditCardEntries`).doc(`${x.id}`).delete()
        .then((res) => console.log(`Success with ${x.id} - ${res}`),
              (res) => console.log(`Failed for ${x.id} - ${res}`));
      }
    });
  }
  ////////////////////////////////////////////////////////////
  // Seva Code - The code above this can be safely deleted. //
  // It is kept as reference for coding and is not a part   //
  // of this program                                        //
  ////////////////////////////////////////////////////////////


  getUser(email){
    console.log(`getUser - email is ${email}`);
    return this.firestore.collection('Users', ref => ref.where("email", "==", email));
  }
  addAppliedUser(id, email, ref, applied, status, refId){
    try {
      return this.firestore.doc<any>(`applied/${id}`).set({ id: id, 
        email: email, 
        reference: ref, 
        appliedFor: applied, 
        approved: status,
        amId: refId
      });
    } catch (e) {
      console.log('fromm firestore.ts');
      
      console.log(e);
      throw e;
    } 
  }
  getHeaders(){
    return this.firestore.collection('headers');
  }
  getAppliedUser(email) {
    console.log(`getAppliedUser - email is ${email}`);
    
    // return this.firestore.collection('applied', ref => ref.where("reference", "==", email).where("approved", "==", false));
    return this.firestore.collection('applied', ref => ref.where("reference", "==", email).where("approved", "==", false));
  }
  updateApprovedUsers(appliedUsers){
    let batch = this.firestore.firestore.batch();
    
    appliedUsers.forEach((a) => {
      // let ref: DocumentReference = this.firestore.collection('applied').doc(a.id);
      if(a.approved == true) {
        let k = {};
        const id = this.firestore.createId();
        if("am" == a.appliedFor) {
          k['amId'] = id;
        } else {
          k['amId'] = a.amId;
        }
        k['approved'] = a.approved;
        if(a.canAdd != undefined) {
          k['canAdd'] = a.canAdd;
        }
        k['email'] = a.email;
        k['id'] = id;
        k['appliedId'] = a.id;
        k['appId'] = this.config.getAppId();
        k['role'] = a.appliedFor;
  
        let ref = this.firestore.collection('applied').doc(a.id).ref;
        batch.update(ref, {approved: a.approved});
        let userRef = this.firestore.collection('Users').doc(id).ref;
        // let rollRef = this.firestore.collection('roles', ref => ref.where("name", "==", a.appliedFor)).ref;
        batch.set(userRef, k);
        // batch.update(userRef, {canAdd: })
      }
    })
    return batch.commit();
  }
  getRole(role) {
    console.log(`getRole - role is ${role}`);

    return this.firestore.collection('roles', ref => ref.where('name', "==", role))
  }
  setReferenceId(id) {
    this.referenceId = id;
  }
  getReferenceId(id) {
   return this.referenceId;
  }
  getUser1() {
    this.auth.getUser().subscribe((au) => {
      if (au == undefined || au == null) {
        return null;
      } else {
        return this.firestore.collection('Users', ref => ref.where("email", "==", au.email).limit(1));
      }
    });
  }
  getCurrentUser() {
    this.auth.getUser().subscribe((au) => {
      if (au == undefined || au == null) {
        this.user$.next(null);
      } else {
        this.firestore.collection('Users', ref => ref.where("email", "==", au.email).limit(1))
            .valueChanges().subscribe((u) => {
              if(u != undefined && u.length > 0) {
                this.user$.next(u[0]);
              } else {
                this.user$.next(null);
              }
            })
        }
      });
    return this.user$;
  }
  saveUser(u) {
    this.firestore.collection('Users').doc(u.id).update(u);
  }
  getUserReference(email) {
    this.firestore.collection('Users', ref => ref.where("email", "==", email))
    .valueChanges().subscribe((u) => {
      if(u.length > 0) {
        this.refUser$.next(u[0]);
      } else {
        this.refUser$.next(null);
      }
      
    });
    return this.refUser$;
  }
  getSPs() {
    if(this.user$.value != undefined && this.user$.value.id != undefined && this.user$.value.id != '') {
      this.firestore.collection('Users', ref => ref.where('amId', "==", this.user$.value.id)
      .where('role', "==", "sp"))
      .valueChanges().subscribe((sps) => {
        if(sps.length > 0) {
          this.sps$.next(sps);
        }
      })
    } else {
      console.log('SUDERROR: This is crap in sp');
    }
    return this.sps$;
  }
  getCustomers() {
    if(this.user$.value != undefined && this.user$.value.id != undefined && this.user$.value.id != '') {
      this.firestore.collection('Users', ref => ref.where('amId', "==", this.user$.value.id)
      .where('role', "==", "cust"))
      .valueChanges().subscribe((cust) => {
        if(cust.length > 0) {
          this.cust$.next(cust);
        }
      })
    } else {
      console.log('SUDERROR: This is crap in cust');
    }
    return this.cust$;
  }
  getCoords() {
    if(this.user$.value != undefined && this.user$.value.id != undefined && this.user$.value.id != '') {
      this.firestore.collection('Users', ref => ref.where('amId', "==", this.user$.value.id)
      .where('role', "==", "co-ord"))
      .valueChanges().subscribe((co) => {
        if(co.length > 0) {
          this.coord$.next(co);
        }
      })
    } else {
      console.log('SUDERROR: This is crap in co-ord');
    }
    return this.coord$;
  }
  saveService(service) {
    let id = this.firestore.createId();
    service['id'] = id;
    return this.firestore.doc(`services/${id}`).set(service);
  }
  getMyServices(id) {
    return this.firestore.collection('services', ref => ref.where('providerId', '==', id));
  }
  getServices(id) {
    return this.firestore.collection('services', ref => ref.where('amId', '==', id));
  }
  getCustRequest(id) {
    return this.firestore.collection('requests', ref => ref.where('customerId', '==', id));
  }
  addRequest(request) {
    let id = this.firestore.createId();
    request['id'] = id;
    return this.firestore.doc(`requests/${id}`).set(request);
  }
  saveActivityTemplates(template) {
    let id = this.firestore.createId();
    template['id'] = id;
    return this.firestore.doc(`activityTemplate/${id}`).set(template);
  }
  getAllActivityTemplates() {
    return this.firestore.collection('activityTemplate');
  }
  getActivityTemplate(id) {
    return this.firestore.collection('activityTemplate', ref => ref.where('id', '==', id));
  }
  saveItinerary(itinerary) {
    let id = this.firestore.createId();
    itinerary['id'] = id;
    itinerary['appId'] = this.config.getAppId();
    return this.firestore.doc(`itinerary/${id}`).set(itinerary);
  }
  updateItinerary(itinerary) {
    return this.firestore.doc(`itinerary/${itinerary.id}`).update(itinerary);
  }
  getAllItinerary() {
    return this.firestore.collection('itinerary', ref => ref.where("appId", "==", this.config.getAppId()));
  }
  deleteItinerary(id) {
    return this.firestore.doc(`itinerary/${id}`).delete();
  }
  getTimeInFSFormat(d) {
    let date = new Date(d);
    date.setHours(date.getUTCHours());
    date.setMinutes(date.getUTCMinutes());
    // firebase.firestore.Timestamp.
    return firebase.firestore.Timestamp.fromDate(date);
  }
  getTimeFromSecs(m) {
    return firebase.firestore.Timestamp.fromMillis(m * 1000);
  }
  getItinerary(id) {
    return this.firestore.collection('itinerary', ref => ref.where("appId", "==", this.config.getAppId())
                                                            .where('id', "==", id));
  }
  eventLog(data) {
    let id = this.firestore.createId();
    data['id'] = id;
    data['appId'] = this.config.getAppId();
    this.firestore.doc(`events/${id}`).set(data);
  }
  setItrCode(itrcode) {
    this.itrcode = itrcode;
  }
  getItrCode() {
    if (this.itrcode != undefined && this.itrcode.itrcode != undefined) {
      return this.itrcode.itrcode;
    } else {
      return null;
    }
  }
}



