import { FirestoreProvider } from './../../providers/firestore/firestore';
import { Component, Input, OnInit } from '@angular/core';
import { OtuConfigProvider } from '../../providers/otu-config/otu-config';
import { OtuAuthProvider } from '../../providers/otu-auth/otu-auth';
import { OtuLoggerProvider } from '../../providers/otu-logger/otu-logger';

/**
 * Generated class for the HeaderComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'header',
  templateUrl: 'header.html'
})
export class HeaderComponent implements OnInit {

  @Input('title') title: string;
  @Input('titleObj') titleObj: string;
  lang: string;
  pageTitle;
  email;

  titles = [{id: 1, title: "Dyna",lang: {en: "Dyna",kn: "ಡೈನಾ"}}, 
            {id: 2, title: "Login", lang: {en: "Login",kn: "ಲಾಗಿನ್"}},
            {id: 3, title: "Approve", lang: {en: "Approve",kn: "ಅನುಮೋದಿಸು"}},
            {id: 3, title: "Signup", lang: {en: "Signup",kn: "ಸೈನ್ ಅಪ್"}},
            {id: 4, title: "AM Home", lang: {en: "AM Home",kn: "AM ಮುಖಪುಟ"}},
            {id: 5, title: "SP Home", lang: {en: "SP Home",kn: "ಎಸ್ಪಿ ಹೋಮ್"}},
            {id: 5, title: "User Home", lang: {en: "User Home",kn: "ಬಳಕೆದಾರ ಹೋಮ್"}},
            {id: 6, title: "Itinerary", lang: {en: "Itinerary",kn: "Itinerary"}}
          ]
  constructor(
                public config: OtuConfigProvider
              , public firestore: FirestoreProvider
              , public auth: OtuAuthProvider
              , private logger: OtuLoggerProvider
            ) {
    console.log('Hello HeaderComponent Component');
    this.config.getLanguage().subscribe((l) => this.lang = l);
  }
  ngOnInit() {
    console.log(this.title);
    if(this.titleObj == undefined) {
      this.config.getCurrentMenu().subscribe((m) => {
        if(m.length> 0) {
          console.log(`Title Pags is ${this.title}`);
          this.titles = m[0].menu.menu;
          console.log(this.titles);
          
          let titleObj = this.titles.find(t => t.title == this.title);
          if(titleObj != undefined) {
            this.pageTitle = titleObj;
          }
        }
        console.log(this.pageTitle);
      });
    } else {
      this.pageTitle = this.titleObj;
    }

    console.log('ionViewDidLoad ApprovalPage');
  }
  changeLanguage() {
    let from = this.lang;
    if (this.lang == "en") {
      this.lang = "kn";
    } else {
      this.lang = "en";
    }
    this.config.setLanguage(this.lang);
    this.logger.eventLog('changeLanguage', {from: from, to: this.lang});
  }
}



