import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ViewController, ModalController, AlertController } from 'ionic-angular';

import { FormBuilder, FormGroup, Validators, FormControl, FormArray } from '@angular/forms';

import { AgeValidator } from '../../validators/age';
import { UsernameValidator } from '../../validators/username';
import { OtuConfigProvider } from '../../providers/otu-config/otu-config';

// import { GenricPopupPage } from '../../pages/genric-popup/genric-popup';

/**
 * Generated class for the DynaFormComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'dyna-form',
  templateUrl: 'dyna-form.html'
})
export class DynaFormComponent {

  @Input('fields') fields: any;

  // fields = [{ name: "firstName", label: { en: "First Name", kn: "ಮೊದಲ ಹೆಸರು" }, validators: [{ name: "required", param: "" }, { name: "minLength", param: "3" }, { name: "maxLength", param: "10" }, {name: "pattern", param: '[a-zA-Z ]*'}], AsyncValidators: [{ name: "checkUsername", param: "" }], dataType: "string", values: [] },
  // { name: "lastName", label: { en: "Last Name", kn: "ಕೊನೆಯ ಹೆಸರು" }, validators: [{ name: "required", param: "" }, { name: "minLength", param: "3" }, { name: "maxLength", param: "10" }, {name: "pattern", param: '[a-zA-Z ]*'}], AsyncValidators: [{ name: "checkUsername", param: "" }], dataType: "string", values: [] },
  // { name: "age", label: { en: "Age", kn: "ವಯಸ್ಸು" }, validators: [{ name: "age", param: "" }], AsyncValidators: [{ name: "checkUsername", param: "" }], dataType: "number", values: [] },
  // { name: "gender", label: { en: "Gender", kn: "ಲಿಂಗ" }, validators: [{ name: "required", param: "" }], AsyncValidators: [{ name: "checkUsername", param: "" }], dataType: "option", values: [{ val: 1, label: { en: "male", kn: "ಪುರುಷ" } }, { val: 2, label: { en: "female", kn: "ಸ್ತ್ರೀ" } }] },
  // { name: "username", label: { en: "Username", kn: "ಬಳಕೆದಾರ ಹೆಸರು" }, validators: [{ name: "required", param: "" }, { name: "minLength", param: "3" }, { name: "maxLength", param: "10" }], AsyncValidators: [{ name: "checkUsername", param: "" }], dataType: "string", values: [] },
  // { name: "privacy", label: { en: "Privacy", kn: "ಗೌಪ್ಯತೆ" }, validators: [{ name: "required", param: "" }], AsyncValidators: [{ name: "checkUsername", param: "" }], dataType: "option", values: [{ val: 1, label: { en: "Public", kn: "ಸಾರ್ವಜನಿಕ" } }, { val: 2, label: { en: "Private", kn: "ಖಾಸಗಿ" } }, { val: 3, label: { en: "Friends", kn: "ಸ್ನೇಹಿತರು" } }] },
  // { name: "bio", label: { en: "Bio", kn: "ಬಯೋ" }, validators: [], AsyncValidators: [{ name: "checkUsername", param: "" }], dataType: "string", values: [] }
  // ];

  @Input('errMessages') errMessages: any;

  // errMessages = [{ errCode: 0, label: { en: "is a required field", kn: "ಅಗತ್ಯ ಕ್ಷೇತ್ರ" } },
  // { errCode: 1, label: { en: "has a miumum length of", kn: "ಮಿಮಿಯಮ್ ಉದ್ದವನ್ನು ಹೊಂದಿದೆ" } },
  // { errCode: 2, label: { en: "has a maximum length of", kn: "ಗರಿಷ್ಠ ಉದ್ದವನ್ನು ಹೊಂದಿದೆ" } },
  // { errCode: 3, label: { en: "? Are you sure?", kn: "? ನೀವು ಖಚಿತವಾಗಿರುವಿರಾ?" } },
  // { errCode: 4, label: { en: "is too young", kn: "ತುಂಬಾ ಚಿಕ್ಕದಾಗಿದೆ" } },
  // { errCode: 5, label: { en: "is not realistic", kn: "ವಾಸ್ತವಿಕವಲ್ಲ" } },
  // { errCode: 6, label: { en: "needs to be a number", kn: "ಒಂದು ಸಂಖ್ಯೆಯ ಅಗತ್ಯವಿದೆ" } },
  // { errCode: 7, label: { en: "Is that a", kn: "ಅದು ಒಂದು" } }
  // ];
  //  
  lang: any;
  lang$;

  @Output('data') data = new EventEmitter();

  submitLabel = { en: "Submit", kn: "ಸಲ್ಲಿಸಲು" };
  public dynaForm: FormGroup;
  dataArrays = {};

  constructor(
                public formBuilder: FormBuilder
              , private config: OtuConfigProvider
              , private viewCtrl: ViewController
              , private mdlCtrl: ModalController
              , private alrtCtrl: AlertController
             ) {
    console.log('Hi From Constructor in Dyna Form');
    this.lang$ = this.config.getLanguage();
    this.lang$.subscribe((l) => this.lang = l);
    this.dynaForm = formBuilder.group({});
  }
  ngOnInit() {
    console.log('Hi From ngOnInit in Dyna Form');
    
    this.fields.forEach((f) => {
      let validatorList = [];
      let asynValidator = [];
      f.validators.forEach((v) => {
        switch (v.name) {
          case "required": {
            validatorList.push(Validators.required);
            break;
          }
          case "minLength": {
            validatorList.push(Validators.minLength(parseInt(v.param)));
            break;
          }
          case "maxLength": {
            validatorList.push(Validators.maxLength(parseInt(v.param)));
            break;
          }
          case "pattern": {
            validatorList.push(Validators.pattern(v.param));
            break;
          }
          case "age": {
            validatorList.push(AgeValidator.isValid);
            break;
          }
          default: {
            //Cry like a baby
            console.log(`Validator: The name is ${v.name} for ${f.name}`);
            break;
          }
        }
      });
      f.AsyncValidators.forEach((v) => {
        switch (v.name) {
          case "checkUsername": {
            asynValidator.push(UsernameValidator.checkUsername);
            break;
          }
          default: {
            //Cry like a baby
            console.log(`AsyncValidator: The name is ${v.name} for ${f.name}`);
            break;
          }
        }
      });
      if(f.dataType == 'stringArray') {
        // this.dynaForm.addControl(f.name, new FormArray([]));
      } else {
        this.dynaForm.addControl(f.name, new FormControl('', validatorList, asynValidator));
      }
    })

  }

  getValidators() {

  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad DynaPage');
  }
  addItem(f) {
    let item = this.mdlCtrl.create("GenricPopupPage", {list: [], label: f.label, lang: this.lang, flag: 1});
    item.onDidDismiss((v) => {
      console.log(v);
      if(v != undefined || v.length == 0) {
        this.dataArrays[f.name] = v;
      }
    });
    item.present();
  }
  save(val) {
    console.log(val);
    let keys = Object.keys(this.dataArrays);
    keys.forEach((k) => {
      val[k] = this.dataArrays[k];
    })
    this.data.emit(val);
  }
  changeLanguage() {
    if (this.lang == "en") {
      this.lang = "kn";
    } else {
      this.lang = "en";
    }
  }
  getErrorMessage(errCode, i) {
    console.log(`${errCode} for ${i} and message is ${this.errMessages[errCode].label[this.lang]}`);

    switch (errCode) {
      case 0: {
        return `${this.fields[i].label[this.lang]} ${this.errMessages[errCode].label[this.lang]}`;
      }
      case 1: {
        let v = this.fields[i].validators.find((x) => { return x.name == 'minLength' });
        let len;
        if (v != undefined) {
          len = v.param;
        }
        return `${this.fields[i].label[this.lang]} ${this.errMessages[errCode].label[this.lang]} ${len}`;
      }
      case 2: {
        let v = this.fields[i].validators.find((x) => x.name == 'maxLength');
        let len;
        if (v != undefined) {
          len = v.param;
        }
        return `${this.fields[i].label[this.lang]} ${this.errMessages[errCode].label[this.lang]} ${len}`;
      }
      case 3: {
        return `${this.fields[i].label[this.lang]} ${this.errMessages[errCode].label[this.lang]}`;
      }
      case 4: {
        return `${this.errMessages[errCode].label[this.lang]}`;
      }
      case 5: {
        return `${this.fields[i].label[this.lang]} ${this.errMessages[errCode].label[this.lang]}`;
      }
      case 6: {
        return `${this.fields[i].label[this.lang]} ${this.errMessages[errCode].label[this.lang]}`;
      }
      case 7: {
        return `${this.errMessages[errCode].label[this.lang]} ${this.fields[i].label[this.lang]}`;
      }
      default: {
        // How in the world???
        console.log("How in the world???");
        return `Just the crap I was looking for`;

      }
    }
  }
}



